@import '~node_modules/modern-normalize/modern-normalize.css';

body{
    font-family: 'Montserrat';
}

.container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
  
    @media screen and (min-width: 480px) {
      width: 480px;
    }
  
    @media screen and (min-width: 768px) {
      width: 768px;
    }
    
    @media screen and (min-width: 1200px) {
      width: 1200px;
    }
  }